.rightbar {
    flex: 3;
}
.rightbarWrapper {
    padding: 20px 20px 0 0;
}
.birthdayContainer {
    display: flex;
    align-items: center;
}
.bithdayImg {
    width: 40px;
    height: 40px;
    margin-right: 10px;
}
.bithdayText {
    font-weight: 300;
    font-size: 15px;
}
.rightbarAd {
    width: 97%;
    border-radius: 10px;
    margin: 30px 0;
}
.rightbarTitle {
    margin-bottom: 20px;
}

.rightbarFriendList {
    margin: 0;
    padding: 0;
    list-style: none;
}
.rightbarFriend {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}
.rightBarProfileImgContainer {
    margin-right: 10px;
    position: relative;
}
.rightbarProfileImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}
.rightbarOnline {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color:limegreen;
    position: absolute;
    top: -2px;
    right: 0;
    border: 2px solid white;
}
.rightbarUsername {
    font-weight: 500;
}
.rightbarTitle {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 10px;
}
.rightbarFollowingImg {
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius:5px ;
}
.rightbarInfo {
    margin-bottom: 30px;
}
.rightbarInfoItem {
    margin-bottom: 10px;
}
.rightbarInfoKey {
    font-weight: 500;
    margin-right: 15px;
    color: #555;
}
.rightbarInfoValue {
    font-weight: 300;
}
.rightbarFollowings {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.rightbarFollowing {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    cursor: pointer;
}